<template>
  <div></div>
</template>

<script>
import MeBaseViewer from "@mesh-3d/core/Source/MeBaseViewer";
function pushScopedId(template, id) {
    let root = document.createElement('div')
    root.innerHTML = template;
    function traverse(nodes, callback) {
        for (let i = 0; i < nodes.length; i++) {
            const node = nodes[i];
            callback(node)
            traverse(node.children, callback)
        }
    }
    traverse(root.children, function (node) {
        node.setAttribute('data-v-' + id, '')
    });
    return root.innerHTML;
}
export default {
  data() {
    return {
      m_config: this.config || {},
    };
  },
  props: {
    config: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  mounted() {
    this._viewer = new MeBaseViewer(
      Object.assign({ container: this.$el }, this.m_config)
    );
    this.$emit("ready", this._viewer);
  },
  destroyed() {
    this._viewer = this._viewer && this._viewer.dispose();
  },
};
</script>

